@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&family=Rationale&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.homeScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem 1rem;
}

.homeScreen h1 {
  font-family: "Rationale", sans-serif;
  text-transform: uppercase;
  color: #d4479c;
  font-size: 3rem;
}

.homeScreen h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.25rem;
  margin: 1rem;
}

.homeScreen .thinDivider {
  width: 100%;
  height: 2px;
  background-color: #d4479c;
}

.homeScreen .icon {
  color: #d4479c;
  font-size: 3rem;
  border: 1px solid #d4479c;
  border-radius: 50%;
  padding: 0.75rem;
  margin: 3rem;
  cursor: pointer;
}

.appBar {
  background-color: #d4479c;
}

.appBar .topLogo {
  display: flex;
  align-items: center;
}

.appBar .topLogo img {
  height: 1.5rem;
  margin-right: 0.5rem;
}

.appBar .topLogo h1 {
  font-family: "Rationale", sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 300;
  color: white;
}

.appBar .white {
  color: white;
}

.mainContent {
  padding: 1rem;
}

body {
  display: -ms-grid;
  display: grid;
  place-items: center;
}

.app {
  width: 375px;
  height: 812px;
  border: 1px solid black;
  margin-top: 1rem;
  border-radius: 1rem;
  overflow: hidden;
}
